body {
  background-color: #283b63;
  font-family: blueviolet;
  color: blanchedalmond;
}

section {
  padding: 40px;
  text-align: center;
}

ul {
  background: #283b63;
  display: flex;
  justify-content: space-around;
  padding: 10px;
}

ul li {
  list-style: none;
}

ul li a {
  color: white;
  text-decoration: none;
}

ul li a:hover {
  color: goldenrod;
}

header {
  text-align: center;
}

#welcome {
  height: 400px;
  background-image: url("assets/pexels-my-kemiktv-youtube-1487883.jpg");
  background-size: cover;
}

h1 {
  font-size: 45px;
  color: white;
  text-shadow: 1px 1px #333;
  text-align: center;
}

.flex-container {
  display: flex;
  justify-content: space-between;
}

#heroes .flex-container div {
  flex: 1 1 0;
}

form {
  margin: 0 auto;
  width: 300px;
  padding-top: 20px;
}

label {
  line-height: 2;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 5px;
  margin-bottom: 5px;
}

input[type="submit"] {
  background: #283b63;
  color: white;
  border: none;
  padding: 5px 10px;
}
